@import '../../styles/main.scss';

.container {
  position: relative;
  margin-bottom: 16px;
  box-sizing: border-box;
  border: none;
  height: 100%;

  @media (min-width: 880px) {
    margin-bottom: 8px;
    flex-grow: 1;
  }
}

.label {
  position: absolute;
  z-index: 2;
  top: -6px;
  left: 16px;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  padding: 4px 6px;
  border-radius: 12px;
  background-color: $white-color;
  color: #8A8B8B;
}

.trigger {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  height: 48px;
  padding-inline-start: 0;
  padding-inline-end: 0;
  padding-block: 0;
  border: none;
  border-radius: 100px;

  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  color: #434343;

  cursor: pointer;
  background-color: $white-color;
}

.trigger_content {
  display: flex;
  box-sizing: border-box;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  justify-content: space-between;
  align-items: center;
  padding: 0px 18px 0px 22px;
  border-radius: 1000px;
  background-color: none;

  &_active {
    background-color: $background-color-white;
  }
}

.text {
  pointer-events: none;
}

.icon {
  pointer-events: none;
}

.icons {
  display: flex;
}

.list {
  position: absolute;
  top: 55px;
  width: 100%;
  max-height: 300px;
  overflow-y: auto;
  scroll-behavior: smooth;
  border: none;
  border-radius: 21px;
  background-color: $white-color;
  box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.05);
  z-index: 10;
}

.listItem {
  position: relative;
  color: $color-grey;
  width: 100%;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  cursor: pointer;
}

.listLink {
  margin: 2px;
  padding: 10px 16px;
  display: block;
  height: 100%;
  border-radius: 22px;

    transition:
    color 0.3s ease,
    background-color 0.3s ease;

  &:hover {
    color: $white-color;
    background-color: $hover-gray;
  }
}
